html {
  box-sizing: border-box;
  height:100%; 
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  position:absolute;
  top:0; 
  bottom:0; 
  right:0; 
  left:0;
  margin: 0;
  padding: 0;
  line-height: 1.5;
  color: #121212;
}

p {
  margin: 0;
}

nav {
  background-color: transparent;
  max-width: 960px;
  margin: 0 auto;
}

#header-static-container {
  height: 300px;
  width: 100%;
  overflow: hidden;
  margin-bottom: 30px;
  position: relative;
}

.header-static{
  width: 100%;
  margin-left: 0px;
  position: absolute;
  left: 0;
}

#bg-foto {
  width: 100vw;
  opacity: 80;
}

#header {
  left: 0;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 1000;
}

.footer {
  background-color: #216b0c;
  color: white;
}

.footer-box {
  width: 960px;
  margin: 0px auto; 
}

.footer-box1 {
  width: 160px;
  margin: 20px 0px; 
}

.footer-box2 {
  width: 250px;
  margin: 30px; 
}

#main-container {
  width: 960px;
  margin: 0px auto;
}

#credit {
  background-color: #2c2b2b;
  color: white;
}

.link {
  color: white;
  padding: 8px;
  text-decoration: none;
  background-color: transparent;
  border: none;
}

.round {
  border-radius: 400px;
  height: 400px;
  width: 400px;
  background-color: #216b0c;
}

